/** @format */

.div-login {
  margin-left: auto;
  margin-right: auto;

  font-style: normal;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin-top: 70px;
}

.div-login .logo-sign-in {
  height: 50px;
  width: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.div-login .h1-sign-in {
  text-align: center;
  color: black;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -0.5px;
  line-height: 36px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  text-align: center;
  height: 36px;
  display: block;
}
.div-login .div-sign-in-user-input {
  text-align: justify;
  font-size: 14px;
  line-height: 21px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  word-spacing: 0;
  background-color: #f6f8fa;
  border-radius: 5px;
  border: solid 1px rgb(204, 204, 204);
  margin: 16px 0 0 0;
  padding: 20px 20px 20px 20px;
  height: 228.6px;
  width: 270px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.div-login .div-sign-in-user-input input {
  width: 266px;
  height: 32px;
  line-height: 20px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  word-spacing: 0px;
  border: 1px solid #e1e4e8;
  margin: 5px 0 15px 0;
  display: block;
  cursor: text;
  box-sizing: border-box;
  box-shadow: rgba(255, 228, 232, 0.2) 0px 1px 0px 0px inset;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.div-login .div-sign-in-user-input h1 {
  font-size: 14px;
  height: 20.8;
  margin: 0 0 7px 0;
  font-weight: 300;
  letter-spacing: -0.5px;
  line-height: 21px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  display: block;
  width: 266px;
}
.div-login .div-sign-in-user-input h1 .link-forgot-password {
  float: right;
  text-decoration: none;
  color: blue;
  font-size: 12px;
}
.div-login .div-sign-in-user-input h1 .link-forgot-password:hover {
  color: rgb(4, 4, 204);
  text-decoration: underline;
}
/*Sign in button*/
.div-login .div-sign-in-user-input .button-Sing-in {
  letter-spacing: 1px;
  background: #2ea44f;
  font-size: 14px;
  font-weight: 500px;
  line-height: 20px;
  -webkit-text-decoration: none solid rgb(255, 255, 255);
          text-decoration: none solid rgb(255, 255, 255);
  text-align: center;
  white-space: nowrap;
  color: white;
  /*BOX*/
  height: 32px;
  width: 266px;
  border: 0px solid;
  border-radius: 5px;
  margin: 20px 0 0 0;
  padding: 5px 16px 5px 16px;
  position: relative;
  display: block;
  cursor: url(data:image/png;base64,), auto;
  outline: none;
}
.div-login .div-sign-in-user-input .button-Sing-in:hover {
  background: #288b44;
  border: none;
}
.div-login .div-sign-in-user-input .button-Sing-in:active {
  background: #38c460;
  border: none;
}
.div-login .div-sign-in-user-input input {
  outline: none;
  box-sizing: border-box;
  padding-left: 15px;
}
.div-login .div-sign-in-user-input input:focus {
  box-shadow: 1px 1px 3px 3px#54b4b4;
}
.div-login .div-new-to-create-account {
  border: solid 0.5px black;
  text-align: justify;
  font-size: 14px;
  line-height: 21px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  word-spacing: 0;
  background-color: transparent;
  border-radius: 5px;
  border: solid 1px rgb(204, 204, 204);
  margin: 16px 0 0 0;
  padding: 20px 20px 20px 20px;
  width: 270px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.div-login .div-new-to-create-account h1 {
  font-size: 14px;
  height: auto;
  margin: 0;
  font-weight: 300;
  line-height: 21px;
  -webkit-text-decoration: none solid rgb(36, 41, 46);
          text-decoration: none solid rgb(36, 41, 46);
  display: inline-block;
  width: 266px;
}
.div-login .div-new-to-create-account .Link-create-account {
  float: right;
  text-decoration: none;
  color: blue;
  font-size: 14px;
}
.div-login .div-new-to-create-account .Link-create-account:hover {
  color: rgb(4, 4, 204);
  text-decoration: underline;
  line-height: normal;
}

