/** @format */

@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");
.ButtonStart {
  background-color: #ffc500;
  color: #fff;
  /*Size*/
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P";
  letter-spacing: 0px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  /*Margin*/
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
}

.ButtonStart:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  transform: translateY(4px);
  transition-duration: 0.03s;
}
.ButtonStart:hover {
  background-color: #fdd44d;
  cursor: url("../../assets/cursors/hover.png"), auto;
}
.Link-start {
  text-decoration: none;
}
