/** @format Nabvar*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap");

.header-navbar {
  top: 0;
  height: 64px;
  align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: sticky;
  width: 100%;
  z-index: 100;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
  padding: auto;
}
.header-navbar .navbarDiv2 StarButton {
  display: block;
  object-fit: contain;
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}

.header-navbar .borderClick {
  width: 0px;
  height: 0px;
}
.header-navbar .navbarDiv3 .borderClick {
  margin-left: 30px;
}
@media only screen and (min-width: 600px) {
  .header-navbar .navbarDiv2 .borderClick {
    margin-left: 30%;
    margin-right: 0;
  }
}
